*{padding:0;margin:0;font-family:"roboto"}:root{--background: #141413;--foreground: #f8faf0}@media(prefers-color-scheme: dark){:root{--background: #141413;--foreground: #f8faf0}}header{width:75vw;display:flex;flex-direction:row;gap:12px;justify-content:flex-end;margin:35px 0 0 0;font-size:10px}body{color:var(--foreground);background:var(--background);font-family:Arial,Helvetica,sans-serif}main{display:flex;justify-content:center}a{text-decoration:none;color:var(--foreground)}.header-container{width:100vw;display:flex;align-items:center;justify-content:center}